import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardFooter,
  Typography,
  Button,
} from "@material-tailwind/react";

const Projects = () => {
  const [repos, setRepos] = useState();

  useEffect(() => {
    let url = "https://api.github.com/users/ArushanSinnadurai/repos";
    fetch(url)
      .then((res) => res.json())
      .then((data) => setRepos(data));
  }, []);

  return (
    <div>
      <h1 className="text-center">Projects</h1>
      <div class="pt-6 pl-40 pr-96">
        <div class="grid grid-cols-[50%_50%] pl-72">
          {repos &&
            repos.map((repo) => (
              <Card className="mt-6 w-96">
                <CardBody>
                  <Typography variant="h5" color="blue-gray" className="mb-2">
                    {repo.full_name.replace("ArushanSinnadurai/", "")}
                  </Typography>
                  <Typography>Need to get readme here</Typography>
                </CardBody>
                <CardFooter className="pt-0">
                  <a href={repo["svn_url"]} className="inline-block">
                    <Button
                      size="sm"
                      variant="text"
                      className="flex items-center gap-2"
                    >
                      Learn More
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        strokeWidth={2}
                        stroke="currentColor"
                        className="h-4 w-4"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          d="M17.25 8.25L21 12m0 0l-3.75 3.75M21 12H3"
                        />
                      </svg>
                    </Button>
                  </a>
                </CardFooter>
              </Card>
            ))}
        </div>
      </div>
    </div>
  );
};

export default Projects;
